export default {
  name: "alert_windows",
  data() {
    return {
      ads_conf: undefined,
      show: false
    }
  },
  created() {
    if (!this.$store.state.user.userinfo.is_vip) {
      var alert_window_time = localStorage.getItem('alert_window:time');
      var now_time = new Date().getTime() / 1000;
      alert_window_time = parseInt(alert_window_time);
      var is_show_alert_window = true;
      if (!isNaN(alert_window_time)) {
        if (now_time - alert_window_time < 600) {
          is_show_alert_window = false;
        }
      }

      if (is_show_alert_window) {
        var alert_dialog = this.$ads_object['alert.dialog'];
        console.log('alert_dialog:', alert_dialog);
        if (alert_dialog) {
          this.ads_conf = alert_dialog[Object.keys(alert_dialog)[0]];
          console.log('this.ads_conf:', this.ads_conf);
          this.show = true;
          localStorage.setItem('alert_window:time', now_time);
        }
      }
    }
  }
}
